.form_control.invalid input {border-color: red;border-style: dashed;border-width: 2px; }
.form_control.invalid label {color: red; }
.message {color: red; }
@media (min-width: 768px) {.form_control {display: flex;justify-content: center;align-items: center;height: 50vh; }
 }
@media (min-width: 1024px) {.form_control {flex-direction: column; }
 }
